import Decimal from './decimal'
export default {
  //盈亏 = (1/均价 - 1/标记价格)* 数量 * 合约规模1
  plVal(direction, avgPrice, markPrice, volume, con, scale = 8) {
    if (avgPrice && markPrice && Number(avgPrice) > 0 && Number(markPrice) > 0) return String(Decimal.accMul(Decimal.accSubtr(Decimal.accDiv(1, avgPrice), Decimal.accDiv(1, markPrice)), Number(con) * Number(volume) * (direction ? 1 : -1), scale))
    return 0
  },
  //盈亏率 = 盈亏/保证金
  plRate(pl, deposit) {
    if (deposit && Number(deposit) > 0) return String(Decimal.accDiv(pl, deposit))
    return 0
  }
}


