<template>
  <div class="app-container public-table">

    <el-input placeholder="请输入搜索内容..." v-model="inputText">
      <el-select v-model="selectType" slot="prepend" style="width: 120px">
        <el-option label="UID" value="encode_uids"></el-option>
        <el-option label="IP" value="ips"></el-option>
        <el-option label="类型" value="user_type"></el-option>
        <el-option label="手机" value="mobiles"></el-option>
        <el-option label="邮箱" value="emails"></el-option>
        <el-option label="时间" value="dates"></el-option>
      </el-select>
      <i v-if="isSelectDate" slot="prefix" class="el-input__icon el-icon-date" @click="searchDate"></i>
      <el-tag v-for="(item, index) in tagList" :key="index" slot="suffix" size="small" closable @close="removeTags(index)">{{item}}</el-tag>
      <i slot="suffix" class="el-input__icon el-icon-success" @click="addTags"></i>
      <el-button slot="append" icon="el-icon-search" @click="submitSearch"></el-button>
    </el-input>
    <el-date-picker class="reset-date-picker" ref="datePicker" @change="datePickerChange" unlink-panels value-format="yyyy-MM-dd"
      v-model="selectDate" slot="prepend" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
    </el-date-picker> 

    <el-table class="table-content" :data="tableList" border show-summary v-loading="loading" height="calc(100% - 120px)" 
    @selection-change="handleSelectionChange" :summary-method="getSummaries">
      <el-table-column type="selection" width="60" align="center"></el-table-column>
      <el-table-column prop="tableUid" label="UID" width="100" align="center"></el-table-column>
      <el-table-column prop="tableUserId" label="UID" width="100" align="center"></el-table-column>
      <el-table-column prop="tableDate" label="时间" align="center" width="160"></el-table-column>
      <el-table-column prop="tablePid" label="仓位ID" align="center" width="120"></el-table-column>
      <el-table-column prop="tableProduct" label="产品" align="center"></el-table-column>
      <el-table-column prop="tableInstructions" label="指令" align="center"></el-table-column>
      <el-table-column prop="tableDirection" label="方向" align="center"></el-table-column>
      <el-table-column prop="tableAmount" label="数量" align="center"></el-table-column>
      <el-table-column prop="tableLever" label="杠杆" align="center"></el-table-column>
      <el-table-column prop="tablePrice" label="开仓均价" align="center"></el-table-column>
      <el-table-column prop="tableMarkPrice" label="标记价格" align="center"></el-table-column>
      <el-table-column prop="tableMargin" label="保证金" align="center"></el-table-column>
      <el-table-column prop="tableCost" label="费用" align="center"></el-table-column>
      <el-table-column prop="tablePlVal" label="盈亏" align="center"></el-table-column>
      <!-- <el-table-column prop="tableRepay" label="补充保证金" align="center"></el-table-column> -->
      <el-table-column prop="tableForce" label="强平价" align="center"></el-table-column>
    </el-table>

    <el-pagination background @size-change="handleSizeChange" @current-change="handlePageChange" 
      :current-page="currentPage" :page-sizes="[20, 30, 40, 50]" :page-size="currentSize" :total="currentTotal"
      layout="total, sizes, prev, pager, next, jumper"
    >
    </el-pagination>

  </div>
</template>

<script>
import TableMixin from '@/mixins/Table'
import Decimal from '@/utils/decimal'
import formula from '@/utils/formula'
import { fetchData } from '@/api'
import { UidEncode } from '@/utils/common'
export default {
  mixins: [TableMixin],
  data() {
    return {
      action: 0,
      searchName: 'searchUserPosition',
      moduleName: 'fetchUserPosition',
      tableList: [],
      multipleSelection: [],
      filterSummaries: ['tableAmount', 'tablePrice', 'tableMargin', 'tableCost', 'tableRepay', 'tableForce']  // 需要统计的字段
    }
  },
  methods: {
    async setListData(data) {
      const tempArray = []
      const markPrice = await fetchData('markPrice').then(data => data ? data : 0)
      // 等待标记价格返回结果之后再执行
      data.forEach(elm => {
        tempArray.push({
          tableUid: UidEncode(elm.UID),
          tableUserId: elm.UID,
          tableDate: elm.CreatedAt.replace('T', ' ').substring(0, 19),
          tablePid: elm.ID,
          tableProduct: elm.CID === 1 ? 'BTCUSD' : elm.CID,
          tableInstructions: elm.Shared ? '平仓' : '开仓',
          tableDirection: elm.Side ? 'LONG' : 'SHORT',
          tableAmount: Decimal.toFixed(elm.Volume, 2),
          tableLever: Decimal.toFixed(elm.Scale, 2) + 'x',
          tablePrice: Decimal.toFixed(elm.Price, 2),
          tableMarkPrice: markPrice,
          tableMargin: Decimal.toFixed(elm.Repay, 2),
          tableCost: Decimal.toFixed(elm.Fee, 2),
          tablePlVal: Decimal.toFixed(formula.plRate(formula.plVal(elm.Side, elm.Price, markPrice, elm.Volume, 1), elm.Repay), 2),
          // tableRepay: Decimal.toFixed(elm.Repay, 2),
          tableForce: Decimal.toFixed(elm.Force, 2)
        })
      })
      this.tableList = tempArray
    }
  }
}
</script>

<style lang="less" scoped>

</style>





